html, body, div {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    transition: .2s all ease-in-out;
}

body {
    background: #000;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif;
}

a {
    color: #fff;
}

.controls {
    position: absolute;
    top: 10px;
    left: 10px;

    display: flex;
    flex-direction: column;

    width: 125px;
    max-width: 25%;

    button {
	background: none;
	border: 2px solid #fff;
	color: #fff;
	padding: 5px 10px;
	font-size: .9em;
	border-radius: 5px;
	box-shadow: 0 0 8px 0 rgba(97, 97, 97, .25);
	margin-bottom: .75em;

	&:disabled,
	&[disabled] {
	    opacity: .4;
	}
    }
}

.drawing {
    opacity: .4;
}

.info {
    position: absolute;
    left: 10px;
    right: 10px;
    bottom: 10px;
    padding: 0;
    margin: 0;
    color: #fff;
    text-align: center;
}

h1, p, button {
    line-height: 1.5em;
    font-size: 1em;
    letter-spacing: .005em;    
}

h1 {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0;
    margin: 0;
    color: #fff;
    text-align: right;
    font-size: 1.5em;
}

